$mod_1: 1.2; // mobile
$mod_2: 1.5; // desktop

h1 {
	font-size: $mod_1 * $mod_1 * $mod_1 * $mod_1 * 1rem;
	margin-bottom: 24px;
	@include fluid-type(
		$min_width,
		$max_width,
		$mod_1 * $mod_1 * $mod_1 * $min_font,
		$mod_2 * $mod_2 * $mod_2 * $min_font
	);
}
h2 {
	font-size: $mod_1 * $mod_1 * $mod_1 * 1rem;
	margin-bottom: 18px;
	@include fluid-type(
		$min_width,
		$max_width,
		$mod_1 * $mod_1 * $mod_1 * $min_font,
		$mod_2 * $mod_2 * $mod_2 * $min_font
	);
}
h3 {
	font-size: $mod_1 * $mod_1 * 1rem;
	margin-bottom: 14px;
	@include fluid-type(
		$min_width,
		$max_width,
		$mod_1 * $mod_1 * $min_font,
		$mod_2 * $mod_2 * $min_font
	);
}
